import React, { Component } from "react";
import { connect } from "react-redux";

import * as actionCreators from "../../store/actions/index";
import Spinner from "../../components/UI/Spinner/Spinner";
import Card from "../../components/Card/Card";
import { shakeArray } from "../../utilities/utilities";
import "./OppositeWords.css";

class OppositeWords extends Component {
  state = {
    url:
      `https://spreadsheets.google.com/feeds/list/12iezPG7J07988QvhFXreRS2UNqjXa30B0l_rS8XJtSU/${this.props.page}/public/values?alt=json`,
      data: null,
    selectedQTY: 0,
    selectedCard: null,
    match: false
  };

  componentDidMount() {
    this.props.fetchData(this.state.url);
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props.fetchedData !== prevProps.fetchedData) {
      let arr = [];
      let data = this.props.fetchedData;
      let dataUpdated = data.map(elem => ({
        word: elem.opposite,
        opposite: elem.word
      }));
      arr = [...data, ...dataUpdated];
      shakeArray(arr);
      this.setState({ data: arr, match: false });
    }
  }

  // shouldComponentUpdate(nextProps, nextState) {
  //   return (
  //     this.state.selectedQTY === nextState.selectedQTY ||
  //     this.state.match !== nextState.match
  //   );
  // }

  checkWords = e => {
    if (this.state.match) {
      this.setState({ match: false });
    }
    let text = e.target.innerText;
    if (this.state.selectedQTY < 2)
      this.setState(prevState => ({
        selectedQTY: prevState.selectedQTY + 1,
        selectedCard: text
      }));
    if (this.state.selectedQTY === 1) {
      if (e.target.dataset.opposite === this.state.selectedCard) {
        document.querySelectorAll(".-selected").forEach(elem => {
          elem.classList.add("-match");
        });
        this.setState({ selectedCard: null, selectedQTY: 0, match: true });
      } else {
        document.querySelectorAll(".-selected").forEach(elem => {
          elem.classList.remove("-selected");
        });
        this.setState({ selectedCard: null, selectedQTY: 0 });
      }
    }
  };

  render() {
    let words = null;
    if (this.props.loading) {
      words = <Spinner />;
    } else if (this.state.data) {
      words = this.state.data.map(elem => (
        <Card
          value={elem.word}
          opposite={elem.opposite}
          key={elem.word}
          click={this.checkWords}
          match={this.state.match}
        />
      ));
    }

    return (
      <div className="OppositeWords">
        <ul>{words}</ul>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    fetchedData: state.wordsConstructor.fetchedData,
    loading: state.wordsConstructor.loading,
    error: state.wordsConstructor.error
  };
};

const mapDispatchToProps = dispatch => {
  return {
    fetchData: url => dispatch(actionCreators.asyncFetchStart(url))
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(OppositeWords);



