import React from "react";
import "./Card.css";

const Card = props => {
  let selectedCard = React.createRef();

  let onClickHandler = e => {
    selectedCard.current.classList.add("-selected");
    props.click(e);
  };

  return (
    <li
      className="Card"
      data-opposite={props.opposite}
      onClick={onClickHandler}
      ref={selectedCard}
      onAnimationEnd={e => (e.target.style.display = "none")}
    >
      {props.value}
    </li>
  );
};

export default Card;



